import React, { useEffect, useState } from "react";
import { Row, Col, Input, Card, Pagination, Spin, Popconfirm } from "antd";
import { Table, Space } from "antd";
import { RiErrorWarningLine } from "react-icons/ri";
import { connect } from "react-redux";
import MasterPageTitle from "../../../../layout/components/content/master-page-title";
import {
  getRepairTypes,
  repairTypesEdit,
  repairTypesDelete,
  isModelVisible,
  SetRepairTypesParams,
} from "../../../../store/Masters/RepairTypes/repairTypesSlice";
import AddRepairTypes from "./addRepairTypes";




function RepairTypes(props) {
  useEffect(() => {
    props.getRepairTypes({ page: 1, search: "", page_size: 10 });
  }, []);

  const columns = [
    {
      title: "S.No",
      dataIndex: "index",
      width: "5%",
    },
    {
      title: "Code",
      dataIndex: "code",
      // sorter: (a, b) => a.name.length - b.name.length,
      filterSearch: true,
      width: "35%",
    },
    {
      title: "Repair Name",
      dataIndex: "name",

      filterMode: "tree",
      filterSearch: true,
      // sorter: (a, b) => a.name.length - b.name.length,
      width: "40%",
    },
    {
      title: "Actions",
      key: "action",
      width: "10%",
      render: (row, record) => (
        <Space size="middle">
          {props.userAccessList?.indexOf("Masters.change_country") !== -1 && (
            <i
              className="ri-edit-box-line hp-mr-2 "
              style={{ cursor: "pointer", color: "#fc6f03" }}
              onClick={() => {
                props.repairTypesEdit(row);
                props.isModelVisible(true);
              }}
            ></i>)}
            {props.userAccessList?.indexOf("Masters.delete_country") !== -1 && (
              <Popconfirm
                placement="topRight"
                title="Are you sure to delete this Record?"
                onConfirm={() => props.repairTypesDelete(record.id)}
                okText="Yes"
                cancelText="No"
                className="hp-ml-8"
                icon={
                  <RiErrorWarningLine className="remix-icon hp-text-color-primary-1" />
                }
              >
                <i
                  className="ri-delete-bin-2-line "
                  style={{ cursor: "pointer", color: "#ff0000" }}
                ></i>
              </Popconfirm>
            )}
        </Space>
      ),
    },
  ];

  const data = props.repairTypeslist.map((row, key) => ({
    ...row,
    index:
      (props.repairTypesParams.page - 1) * props.repairTypesParams.page_size + key + 1,
  }));

  function onPageChange(page, pageSize) {
    props.SetRepairTypesParams({
      ...props.repairTypesParams,
      page_size: pageSize,
      page: page == 0 ? 1 : page,
    });
    props.getRepairTypes({
      ...props.repairTypesParams,
      page_size: pageSize,
      page: page == 0 ? 1 : page,
    });
  }

  function onSearch(val) {
    props.SetRepairTypesParams({ ...props.repairTypesParams, search: val });
    props.getRepairTypes({ ...props.repairTypesParams, search: val });
  }
  function showTotal(total) {
    return "Total " + total + " items";
  }
  const { Search } = Input;
  return (
    <Row gutter={[32, 32]} className="hp-mt-32">
      <MasterPageTitle
        pageTitle="Repair Types Master"
        addModelTile={
          props.userAccessList?.indexOf("Masters.add_country") !== -1 &&
          "Add Repair Type"
        }
        goBack={true}
        setIsModalVisible={props.isModelVisible}
        isModalVisible={props.model}
      />

      <Col span={24}>
        <Card>
          <Row>
            <Col span={12} xs={24} sm={24} md={12} className="hp-mb-12">
              <Search
                placeholder="Search..."
                allowClear
                onChange={(e) => {
                  if (e.target.value == "") {
                    props.getRepairTypes({
                      ...props.repairTypesParams,
                      page: 1,
                      search: "",
                    });
                    props.SetRepairTypesParams({
                      ...props.repairTypesParams,
                      page: 1,
                      search: "",
                    });
                  }
                }}
                onSearch={(value) => {
                  onSearch(value);
                }}
              />
            </Col>

            <Col span={24}>
              <Spin tip="Loading..." spinning={props.loading} delay={500}>
                <Table
                  columns={columns}
                  dataSource={data}
                  scroll={{ x: 500 }}
                  pagination={false}
                  size="middle"
                />
              </Spin>
            </Col>
            <Col span={24}>
              <Pagination
                size="small"
                current={props.repairTypesParams?.page}
                pageSize={props.repairTypesParams?.page_size}
                total={props.listCount}
                showTotal={showTotal}
                showSizeChanger
                onChange={onPageChange}
                className="hp-mb-12 hp-mt-24 hp-float-right"
              />
            </Col>
          </Row>
        </Card>
      </Col>
      <AddRepairTypes />
    </Row>
  );
}

function mapStateToProps({ repairTypes, system }) {0
  return {
    repairTypeslist: repairTypes.repairTypeslist,
    noofpages: repairTypes.noofpages,
    listCount: repairTypes.listCount,
    repairTypesParams: repairTypes.repairTypesParams,
    loading: repairTypes.loading,
    userAccessList: system.userAccessList,
  };
}

export default connect(mapStateToProps, {
  getRepairTypes,
  repairTypesEdit,
  repairTypesDelete,
  isModelVisible,
  SetRepairTypesParams,
})(RepairTypes);
